import {React,Component} from 'react';
import '../App.css';
class CardComponent extends Component
{
    constructor(props) {
        super(props);
        this.state = {};
        //console.log("Card Props:");
        //console.log(props);
        this.state = {
                VehicleNumber: props.VehicleNumber,
                SuperOnClickFunc: props.SuperOnClickFunc,
                Hidden: props.HiddenState,
                Index: props.UniqueIndex,
                TransformValue: "rotateY(0deg)",
                
        }
        this.cardOnClick = this.cardOnClick.bind(this);
        //console.log("Index: " + this.state.Index);
    }
    cardOnClick() {
        //console.log("Card Cliked: %d", this.state.VehicleNumber);
        this.state.SuperOnClickFunc(
            {
                "Number": this.state.VehicleNumber,
                "Index": this.state.Index
            }
        );
    }
    componentDidUpdate(prevProps, prevState) {
        if (this.props.HiddenState !== prevState.Hidden) {
            this.setState({ Hidden: this.props.HiddenState});
        }
        if (this.props.Transform !== prevState.TransformValue) {
            this.setState({TransformValue: this.props.Transform});
        }
    }
    
    render() {
        return (
            <div className='flip-card' onClick={this.cardOnClick}>
                <div className='flip-card-inner' style={{transform: this.state.TransformValue, visibility: (this.state.Hidden==="REMOVED"?"hidden":"visible")}}>
                    <div className='flip-card-front'>
                        <img alt="cardfront" src={process.env.PUBLIC_URL + '/images/cardfront.png'} style={{width:"125px", height: "75px"}} />
                    </div>
                    <div className='flip-card-back'>
                        <img alt="cardrear" className="flip-card" src={process.env.PUBLIC_URL  + '/images/Vehicle_' + this.state.VehicleNumber + '.jpg'}  style={{ width: "125px", height: "75px"}} />
                    </div>
                </div>
            </div>
        );
    }
}

export default CardComponent;