import Button from '@restart/ui/esm/Button';
import {React, Component} from 'react'
import { Container, Modal, Nav, Navbar } from 'react-bootstrap';

import CardComponent from './CardComponent';
class MemoryGameComponent extends Component
{
    constructor(props) {
        super(props);
        this.state = {
            CardStates : [],
            SelectedCards: [],
            GeneratedList: [],
            CardMappingList: [],
            CardList: [],
            RotateStates: [],
            IndexList: [],
            CardsLocked: false,
            TimerTime: 180,
            GameEnd: false,
            GameEndTitle: "",
            GameEndReason: "",
            Timer: null
        };
        this.onCardClick = this.onCardClick.bind(this);
        this.flipCard = this.flipCard.bind(this);
        this.unflipCard = this.unflipCard.bind(this);
        this.BackgroundTimer = this.BackgroundTimer.bind(this);
    }
    componentDidMount() {
        //PreRendering Code...
        this.generateRandomNumbers(18, 0, 211);
        this.setState({
            Timer: setInterval(this.BackgroundTimer, 1000, true)
        });
    }
    componentWillUnmount() {
        clearInterval(this.state.Timer);
    }
    //Custom functions
    generateRandomNumbers(listLength, startRange, endRange) {
        let list = [];
        let indices = [];
        while(list.length < listLength) {
            let randomNum = ( Math.round(Math.random() * 1000) % (endRange + 1) ) + startRange;
            if(!list.includes(randomNum)) list.push(randomNum);
        }
    
        let shuffledList = this.shuffleList(list);
        for(let i = 0; i < shuffledList.length; i++) {
            list.push(shuffledList[i]);
            
        }
        for(let i = 0; i < list.length; i++) {
            indices.push(i);
        }
        
        // We'll now arrange the cards in a 6 x 6 manner
        let finalList = [];
        let finalListStates = [];
        let mappingList = [];
        let rotateStates = [];
        let j = 0;
        for(let i = 0; i < 6; i++) {
            finalList.push([]);
            finalListStates.push([]);
            rotateStates.push([]);
            for(let k = 0; k < 6; k++)
            {
                finalList[i].push(list[j]);
                finalListStates[i].push("HIDDEN");
                mappingList.push([list[j], i,k]);
                rotateStates[i].push("rotateY(0deg)");
                j++;
            }
        }
        
        this.setState({
            GeneratedList: finalList,
            CardStates: finalListStates,
            CardMappingList: mappingList,
            CardList: list,
            RotateStates: rotateStates,
            IndexList: indices,

        })
    }
    shuffleList(list) {

		//We first create an array which will hold the indices which we will later assign
		let shuffleIndices = [];
		while(shuffleIndices.length < list.length) {
		    let random = Math.round(Math.random() * 100) % list.length;
		    if(!shuffleIndices.includes(random)) shuffleIndices.push(random);
		}

		// We then store the new list in this variable
		let tempList = [];
		for(let i = 0; i < shuffleIndices.length; i++) {
			tempList[i] = list[ shuffleIndices[i] ];
		}
		return tempList;
	}
    flipCard(i, j) {
        let rotateStates = this.state.RotateStates;
        rotateStates[i][j] = "rotateY(180deg)";
        this.setState(
            {
                RotateStates: rotateStates
            }
        );
    }
    unflipCard(i, j) {
        let rotateStates = this.state.RotateStates;
        rotateStates[i][j] = "rotateY(0deg)";
        this.setState(
            {
                RotateStates: rotateStates
            }
        );
    }
    onCardClick(clickObject) {
        if(this.state.CardsLocked) return;
        let cardnum = clickObject.Number;
        let index = clickObject.Index;
        let array = this.state.SelectedCards;
        let rotateStates = this.state.RotateStates;

        for(let i = 0; i < this.state.CardMappingList.length; i++) {
            if(this.state.CardMappingList[i][0] === cardnum && this.state.IndexList[i] === index) {
                let x = this.state.CardMappingList[i][1];
                let y = this.state.CardMappingList[i][2];
                rotateStates[x][y] = "rotateY(180deg)";
            }
        }
        array.push([cardnum, index]);
        if(array.length > 1) {
            if(array[0][1] === index) {
                array.splice(1, 1);
            }
        }
        this.setState(
            {
                SelectedCards: array,
                RotateStates: rotateStates
            }
        );
        
        if(this.state.SelectedCards.length >= 2) {
            if(this.state.SelectedCards[0][0] !== this.state.SelectedCards[1][0]) {
                this.setState( {
                    CardsLocked: true
                });
                let This = this;
                setTimeout(
                    function() {
                        for(let i = 0; i < This.state.CardMappingList.length; i++) {
                            if(This.state.CardMappingList[i][0] === This.state.SelectedCards[0][0]) {
                                let x = This.state.CardMappingList[i][1];
                                let y = This.state.CardMappingList[i][2];
                                rotateStates[x][y] = "rotateY(0deg)";
                            }
                            if(This.state.CardMappingList[i][0] === This.state.SelectedCards[1][0]) {
                                let x = This.state.CardMappingList[i][1];
                                let y = This.state.CardMappingList[i][2];
                                rotateStates[x][y] = "rotateY(0deg)";
                            }
                        }
                        //Hide those cards
                        This.setState(
                            {
                                SelectedCards: [],
                                RotateStates: rotateStates,
                                CardsLocked: false
                            }
                        );
                    },
                    1000);
                
            } else { // They match
                let This = this;
                setTimeout(function() {
                    let cardStates = This.state.CardStates;
                
                    let cardList = This.state.CardList;
                    for(let i = 0; i < This.state.CardMappingList.length; i++) {
                        if(This.state.CardMappingList[i][0] === This.state.SelectedCards[0][0]) {
                            let x = This.state.CardMappingList[i][1];
                            let y = This.state.CardMappingList[i][2];
                            if(cardStates[x][y] !== "REMOVED") {
                                cardStates[x][y] = "REMOVED";
                            }
                        }
                    }
                    cardList.splice(cardList.indexOf(This.state.SelectedCards[0][0]), 1);
                    cardList.splice(cardList.indexOf(This.state.SelectedCards[0][0]), 1); 
                    This.setState({
                        SelectedCards: [],
                        CardStates: cardStates
                    });
                    if(cardList.length <= 0) {
                        This.setState({
                            GameEnd: true,
                            GameEndTitle: "You've won!",
                            GameEndReason: "You have won the game, you may restart it by refreshing this page!"
                        });
                        clearInterval(This.state.Timer);
                    }
                    
                },500)
                
                
            }
        }
    }
    BackgroundTimer() {
        
        let tt = this.state.TimerTime;
        if(tt > 0) {
            tt--;
            this.setState({
                TimerTime: tt
            });
        } else {
            this.setState({
                GameEnd: true,
                GameEndTitle: "Time's up!",
                GameEndReason: "You could not outrun the clock, time's up and you lose the game. You may restart it by refreshing this page!"
            });
        }
    }
    render() {
        let kIdx = -1;
        return (
            <div>
                <Container align="center">
                    <Navbar>
                        <Navbar.Brand>
                            Memory Game - By Siddharth Shenoy
                        </Navbar.Brand>
                        <Nav.Link href="#">{
                            (
                                () => {
                                    let minutes = Math.floor(this.state.TimerTime/60);
                                    let seconds = this.state.TimerTime%60;
                                    if(minutes < 10) { 
                                        minutes = "0" + minutes;
                                    }
                                    if(seconds < 10) {
                                        seconds = "0" + seconds;
                                    }
                                    return minutes + ":" + seconds;
                                }
                            )()
                            
                        }</Nav.Link>
                    </Navbar>
                    <table>
                        <tbody>
                        {
                            this.state.GeneratedList.map((object, index) => {
                                
                                return (
                                    <tr key={index}>
                                        {
                                            object.map((obj, idx) => {
                                                kIdx++;   
                                                return (
                                                    <td key={idx} style={{width: "125px", height: "75px", padding: "5px"}}>
                                                        <CardComponent VehicleNumber={obj} UniqueIndex={this.state.IndexList[kIdx]} SuperOnClickFunc={this.onCardClick} HiddenState={this.state.CardStates[index][idx]} Transform={this.state.RotateStates[index][idx]}/>
                                                    </td>
                                                )
                                                
                                            })
                                            
                                        }
                                    </tr>
                                );
                            })
                        }
                        </tbody>
                    </table>
                </Container>
                <Modal show={this.state.GameEnd} onHide={this.hideModal}>
                    <Modal.Header >
                        <Modal.Title>{this.state.GameEndTitle}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>{this.state.GameEndReason}</Modal.Body>
                </Modal>
            </div>
        );
    }
}

export default MemoryGameComponent;